import Link from 'next/link'

const Post = props => (
    <li>
        <Link href={`/blogpost?title=${props.title}`}>
            <a>{props.title}</a>
        </Link>
    </li>
)

const Index = () => {
    return(
        <div>
            <h1>Single page blog - by me</h1>
            <Post title='first' />
            <Post title='second' />
            <Post title='final' />
        </div>
    )
}

export default Index